/* TRANG THEO DÕI KHÓA HỌC */
.InProgressCourses {
    padding: 32px 0;
    background-color: #f3f3f3;
}
@media (max-width:572px) {
    .InProgressCourses {
        padding: 16px 8px;
    }
}
.InProgressCourses .center {
    position: relative;
    max-width: 956px;
    margin: auto;
    background: #fff;
}
.InProgressCourses .Inprogress {
    position: relative;
    width: 100%;
    box-shadow: rgba(31, 31, 31, 0.12) 0px 1px 6px, rgba(31, 31, 31, 0.12) 0px 1px 4px;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    border-radius: 3px;
    overflow: hidden;
}
.InProgressCourses .Inprogress header {
    font-size: 14px;
    line-height: 21px;
}
.InProgressCourses .Inprogress header .CourseHeader {
    padding: 11px 28px;
    background: #20589d;
    position: relative;
    color: #fff;
    min-height: 40px;
}
.InProgressCourses .course-main-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 198px;
    justify-content: center;
    padding: 10px 28px;
}
@media (max-width:572px) {
    .InProgressCourses .course-main-content {
        height: auto;
    }
}
.InProgressCourses .course-main-content .course-main-content-center {
    display: flex;
    width: 100%;
    position: relative;
}
@media (max-width: 572px) {
    .InProgressCourses .course-main-content .course-main-content-center .course-main-content-primary {
        padding: 0;
    }
}
.InProgressCourses .course-main-content .course-main-content-center .course-main-content-primary a h1 {
    color: #3278ce;
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 2px;
    font-weight: 700;
}
.InProgressCourses .course-main-content .course-main-content-center .course-main-content-primary span {
    font-size: 0.75rem;
    line-height: 1.125rem;
    text-align: center;
    vertical-align: center;
    border: 1px solid #2163b2;
    border-radius: 100px;
    padding: 2px 10px;
}
.InProgressCourses .course-main-content .course-main-content-center .course-main-content-secondary {
    display: flex;
    justify-content: center;
    align-items: center;
}
.InProgressCourses
    .course-main-content
    .course-main-content-center
    .course-main-content-secondary
    .course-main-content-button {
    display: flex;
    flex-direction: column;
    max-width: 200px;
    align-items: center;
    width: 100%;
    margin-left: auto;
}
@media (max-width: 572px) {
    .InProgressCourses
        .course-main-content
        .course-main-content-center
        .course-main-content-secondary
        .course-main-content-button {
        margin-right: auto;
        margin-top: 8px;
    }
}
.InProgressCourses
    .course-main-content
    .course-main-content-center
    .course-main-content-secondary
    .course-main-content-button
    .Course-link-button
    a {
    min-width: 180px;
    text-align: center;
    font-weight: 700;
    background-color: rgb(42, 115, 204);
    font-size: 0.8rem;
    min-height: 36px;
    color: rgb(255, 255, 255) !important;
    border-radius: 2px;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(42, 115, 204);
    border-image: initial;
    padding: 0.5rem 1rem;
    display: inline-block;
    line-height: 1rem;
    user-select: none;
    position: relative;
}
.InProgressCourses .Course-footer {
    border-top: 1px solid #d2d2d2;
    margin: 0 28px;
    padding: 15px 0;
    font-size: 0.625rem;
}
.InProgressCourses .Course-footer .partner-name {
    text-transform: uppercase;
    font-size: 0.875rem;
    padding-left: 10px;
}
